<template>
  <v-expansion-panel class="panel-type-address">
    <v-expansion-panel-header>{{ use }} {{ name }}</v-expansion-panel-header>
    <v-expansion-panel-content>
      <v-form @submit.prevent>
        <v-container>
          <v-text-field
            v-model.lazy="use"
            :rules="rules.requiredString25"
            :counter="25"
            :label="labelUse"
            required
          />
          <v-text-field
            v-model.lazy="name"
            :rules="rules.requiredString25"
            :counter="25"
            :label="labelName"
            required
          />
          <div class="d-flex flex-row">
            <v-text-field
              class="mr-2 width-70vh"
              v-model.lazy="street"
              :rules="rules.requiredString25"
              :counter="25"
              label="Strasse"
              required
            />
            <v-text-field
              v-model.lazy="nr"
              :rules="rules.requiredString5"
              :counter="5"
              label="Nr."
              required
            />
          </div>
          <v-text-field
            v-model.lazy="additional"
            :rules="rules.string25"
            :counter="25"
            label="Adresszusatz, c/o, Stock"
          />
          <div class="d-flex flex-row">
            <v-text-field
              class="mr-2"
              v-model.lazy="postcode"
              :rules="rules.requiredString5"
              :counter="5"
              label="PLZ"
              required
            />
            <v-text-field
              class="width-70vh"
              v-model.lazy="location"
              :rules="rules.requiredString25"
              :counter="25"
              label="Ort"
              required
            />
          </div>

          <div class="d-flex flex-row mt-3">
            <div class="ml-auto"/>
            <v-btn text dense color="error" class="pa-0" @click="$emit('remove')"><v-icon>mdi-minus</v-icon> Entfernen</v-btn>
          </div>
        </v-container>
      </v-form>
    </v-expansion-panel-content>
  </v-expansion-panel>
</template>

<script>
import { recObjectByPath } from '@/helper/static'

export default {
  name: 'move-step3-panel-type-address',
  props: {
    path: String,
    idx: Number,
    labelUse: String,
    labelName: String
  },
  data: () => ({
    rules: {
      string25: [
        v => v.length <= 25 || ' '
      ],
      requiredString25: [
        v => !!v || 'Pflichtfeld',
        v => v.length <= 25 || ' '
      ],
      requiredString5: [
        v => !!v || 'Pflichtfeld',
        v => v.length <= 5 || ' '
      ],
      requiredString: [
        v => !!v || 'Pflichtfeld'
      ]
    }
  }),
  computed: {
    use: {
      get() {
        return recObjectByPath(this.$store.getters, this.path).use
      },
      set(value) {
        this.$store.commit('updateProperty', { path: `${this.path}.use`, value })
      }
    },
    name: {
      get() {
        return recObjectByPath(this.$store.getters, this.path).name
      },
      set(value) {
        this.$store.commit('updateProperty', { path: `${this.path}.name`, value })
      }
    },
    street: {
      get() {
        return recObjectByPath(this.$store.getters, this.path).street
      },
      set(value) {
        this.$store.commit('updateProperty', { path: `${this.path}.street`, value })
      }
    },
    nr: {
      get() {
        return recObjectByPath(this.$store.getters, this.path).nr
      },
      set(value) {
        this.$store.commit('updateProperty', { path: `${this.path}.nr`, value })
      }
    },
    additional: {
      get() {
        return recObjectByPath(this.$store.getters, this.path).additional
      },
      set(value) {
        this.$store.commit('updateProperty', { path: `${this.path}.additional`, value })
      }
    },
    postcode: {
      get() {
        return recObjectByPath(this.$store.getters, this.path).postcode
      },
      set(value) {
        this.$store.commit('updateProperty', { path: `${this.path}.postcode`, value })
      }
    },
    location: {
      get() {
        return recObjectByPath(this.$store.getters, this.path).location
      },
      set(value) {
        this.$store.commit('updateProperty', { path: `${this.path}.location`, value })
      }
    }
  }
}
</script>

<style scoped>
.width-70vh {
  width: 70vh;
}

.width-50vh {
  width: calc(50% - 1rem);
}
</style>