<template>
  <div>
    <Steps />
  </div>
</template>

<script>
import Steps from '@/components/move/Steps'

import { mapActions } from 'vuex'

export default {
  name: 'move',
  components: {
    Steps
  },
  methods: {
    ...mapActions([
      'loadFromLocalDB'
    ])
  },
  async created() {
    // fetch move in store
    const UUID = this.$route.params.uuid

    this.loadFromLocalDB(UUID)
  }
}
</script>

