<template>
  <div class="wrapped">
    <span>Bitte erfassen Sie Ihren bisherigen und neuen Wohnort.</span><br/>
    
    <div :class="{'d-flex': true, 'flex-row': !$vuetify.breakpoint.xsOnly, 'flex-column': $vuetify.breakpoint.xsOnly, 'full-width': true}">
      <div class="d-flex flex-column full-width">
        <Address
          class="ma-1 mt-3"
          title="Bisherige Adresse" 
          address="currentAddress"
          :showDates="true"
          date-label="Auszugsdatum"
          key-label="Schlsselbergabe" />
        <v-checkbox dense hide-details v-model="billCurrentAddress" label="Allfllige Schlussrechnungen an diese Adresse" class="ml-2"/>
        <v-checkbox dense hide-details v-model="billOtherAddress" label="Allfllige Schlussrechnungen an andere Adresse" class="ml-2"/>
        <Address v-if="billOtherAddress"
          class="ma-1 mt-3 mb-3"
          title="Andere Adresse" 
          address="otherAddress"
          :showDates="false"/>
      </div>
      <div :class="{ 'ml-10': !$vuetify.breakpoint.xsOnly }">
        <Address
          class="ma-1 mt-3"
          title="Neue Adresse" 
          address="newAddress"
          :showDates="true"
          date-label="Einzugsdatum"
          key-label="Schlsselbernahme" />
        <v-checkbox dense hide-details v-model="billNewAddress" label="Allfllige Schlussrechnungen an diese Adresse" class="ml-2"/>
      </div>
    </div>

    <NextPrevBar :show-next="true" :show-prev="false" />
  </div>
</template>

<script>
import Address from '@/components/move/step1/Address'

import NextPrevBar from '@/components/move/NextPrevBar'

export default {
  name: 'move-step1',
  components: {
    Address,
    NextPrevBar
  },
  methods: {
    billClick(field) {
      this.$store.commit('updateProperty', { path: 'billing.billCurrentAddress', value: ('billCurrentAddress' === field) })
      this.$store.commit('updateProperty', { path: 'billing.billNewAddress', value: ('billNewAddress' === field) })
      this.$store.commit('updateProperty', { path: 'billing.billOtherAddress', value: ('billOtherAddress' === field) })
    }
  },
  data: () => ({
  }),
  computed: {
    billCurrentAddress: {
      get() {
        return this.$store.getters.billing.billCurrentAddress
      },
      set() {
        this.billClick('billCurrentAddress')
      }
    },
    billNewAddress: {
      get() {
        return this.$store.getters.billing.billNewAddress
      },
      set() {
        this.billClick('billNewAddress')
      }
    },
    billOtherAddress: {
      get() {
        return this.$store.getters.billing.billOtherAddress
      },
      set() {
        this.billClick('billOtherAddress')
      }
    }
  }
}
</script>

