<template>
  <v-expansion-panel class="panel-type-friends">
    <v-expansion-panel-header>{{ firstname }} {{ lastname }}</v-expansion-panel-header>
    <v-expansion-panel-content>
      <v-form @submit.prevent>
        <v-container>
          <v-text-field
            v-model.lazy="firstname"
            :rules="rules.requiredString25"
            :counter="25"
            label="Vorname"
            required
          />
          <v-text-field
            v-model.lazy="lastname"
            :rules="rules.requiredString25"
            :counter="25"
            label="Nachname"
            required
          />
          <v-text-field
            v-model.lazy="mail"
            :rules="rules.requiredString50Mail"
            :counter="50"
            label="E-Mail"
            type="mail"
            required
          />

          <div class="d-flex flex-row mt-3">
            <div class="ml-auto"/>
            <v-btn text dense color="error" class="pa-0" @click="$emit('remove')"><v-icon>mdi-minus</v-icon> Entfernen</v-btn>
          </div>
        </v-container>
      </v-form>
    </v-expansion-panel-content>
  </v-expansion-panel>
</template>

<script>
import { recObjectByPath } from '@/helper/static'

export default {
  name: 'move-step3-panel-type-friends',
  props: {
    path: String,
    idx: Number
  },
  data: () => ({
    rules: {
      string25: [
        v => v.length <= 25 || ' '
      ],
      requiredString25: [
        v => !!v || 'Pflichtfeld',
        v => v.length <= 25 || ' '
      ],
      requiredString50Mail: [
        v => !!v || 'Pflichtfeld',
        v => v.length <= 50 || ' ',
        v => /\S+@\S+\.\S+/.test(v) || 'Keine gltige E-Mail'
      ],
      requiredString5: [
        v => !!v || 'Pflichtfeld',
        v => v.length <= 5 || ' '
      ],
      requiredString: [
        v => !!v || 'Pflichtfeld'
      ]
    }
  }),
  computed: {
    firstname: {
      get() {
        return recObjectByPath(this.$store.getters, this.path).firstname
      },
      set(value) {
        this.$store.commit('updateProperty', { path: `${this.path}.firstname`, value })
      }
    },
    lastname: {
      get() {
        return recObjectByPath(this.$store.getters, this.path).lastname
      },
      set(value) {
        this.$store.commit('updateProperty', { path: `${this.path}.lastname`, value })
      }
    },
    mail: {
      get() {
        return recObjectByPath(this.$store.getters, this.path).mail
      },
      set(value) {
        this.$store.commit('updateProperty', { path: `${this.path}.mail`, value })
      }
    }
  }
}
</script>

<style scoped>
.width-70vh {
  width: 70vh;
}

.width-50vh {
  width: calc(50% - 1rem);
}
</style>