<template>
  <v-card class="home-address-card" :elevation="3">
    <v-card-title class="headline">{{ title }}</v-card-title>

    <v-form @submit.prevent>
      <v-container>
        <div class="d-flex flex-row">
          <v-text-field
            class="mr-2 width-70vh"
            v-model.lazy="street"
            :rules="rules.requiredString25"
            :counter="25"
            label="Strasse"
            required
          />
          <v-text-field
            v-model.lazy="nr"
            :rules="rules.requiredString5"
            :counter="5"
            label="Nr."
            required
          />
        </div>
        <v-text-field
          v-model.lazy="additional1"
          :rules="rules.string25"
          :counter="25"
          label="Adresszusatz 1, c/o, Stock"
        />
        <v-text-field
          v-model.lazy="additional2"
          :rules="rules.string25"
          :counter="25"
          label="Adresszusatz 2, c/o, Stock"
        />
        <div class="d-flex flex-row">
          <v-text-field
            class="mr-2"
            v-model.lazy="postcode"
            :rules="rules.requiredString5"
            :counter="5"
            label="PLZ"
            required
          />
          <v-text-field
            class="width-70vh"
            v-model.lazy="location"
            :rules="rules.requiredString25"
            :counter="25"
            label="Ort"
            required
          />
        </div>
        <div class="d-flex flex-row mt-6 full-width" v-if="showDates">
          <v-text-field
            class="mr-2 width-50vh"
            v-model.lazy="date"
            :rules="rules.requiredString"
            :label="dateLabel"
            type="date"
            required
          />
          <v-text-field
            class="width-50vh"
            v-model.lazy="key"
            :rules="rules.requiredString"
            :label="keyLabel"
            type="date"
            required
          />
        </div>
      </v-container>
    </v-form>

  </v-card>
</template>

<script>
export default {
  name: 'home-step1-address',
  props: {
    title: String,
    address: String,
    showDates: Boolean,
    dateLabel: String,
    keyLabel: String
  },
  data: () => ({
    rules: {
      string25: [
        v => v.length <= 25 || ' '
      ],
      requiredString25: [
        v => !!v || 'Pflichtfeld',
        v => v.length <= 25 || ' '
      ],
      requiredString5: [
        v => !!v || 'Pflichtfeld',
        v => v.length <= 5 || ' '
      ],
      requiredString: [
        v => !!v || 'Pflichtfeld'
      ]
    }
  }),
  computed: {
    street: {
      get() {
        return this.$store.getters.addresses[this.address].street
      },
      set(value) {
        this.$store.commit('updateProperty', { path: `addresses.${this.address}.street`, value })
      }
    },
    nr: {
      get() {
        return this.$store.getters.addresses[this.address].nr
      },
      set(value) {
        this.$store.commit('updateProperty', { path: `addresses.${this.address}.nr`, value })
      }
    },
    additional1: {
      get() {
        return this.$store.getters.addresses[this.address].additional1
      },
      set(value) {
        this.$store.commit('updateProperty', { path: `addresses.${this.address}.additional1`, value })
      }
    },
    additional2: {
      get() {
        return this.$store.getters.addresses[this.address].additional2
      },
      set(value) {
        this.$store.commit('updateProperty', { path: `addresses.${this.address}.additional2`, value })
      }
    },
    postcode: {
      get() {
        return this.$store.getters.addresses[this.address].postcode
      },
      set(value) {
        this.$store.commit('updateProperty', { path: `addresses.${this.address}.postcode`, value })
      }
    },
    location: {
      get() {
        return this.$store.getters.addresses[this.address].location
      },
      set(value) {
        this.$store.commit('updateProperty', { path: `addresses.${this.address}.location`, value })
      }
    },
    date: {
      get() {
        return this.$store.getters.addresses[this.address].date
      },
      set(value) {
        this.$store.commit('updateProperty', { path: `addresses.${this.address}.date`, value })
      }
    },
    key: {
      get() {
        return this.$store.getters.addresses[this.address].key
      },
      set(value) {
        this.$store.commit('updateProperty', { path: `addresses.${this.address}.key`, value })
      }
    }
  },
  methods: {
    
  }
}
</script>

<style scoped>
.home-address-card {
  width: 95%;
  max-width: 350px;
}

.width-70vh {
  width: 70vh;
}

.width-50vh {
  width: calc(50% - 1rem);
}
</style>