      <v-stepper-step editable step="1" color="success">Angaben zum Umzug</v-stepper-step>
      <v-stepper-content step="1">
        <Step1/>
      </v-stepper-content>
      
      <v-stepper-step editable step="2" color="success">Wer zieht um?</v-stepper-step>
      <v-stepper-content step="2">
        <Step2/>
      </v-stepper-content>

      <v-stepper-step editable step="3" color="success">Wer soll informiert werden?</v-stepper-step>
      <v-stepper-content step="3">
        <Step3 />
      </v-stepper-content>

      <v-stepper-step editable step="4" color="success">Alles unter Kontrolle</v-stepper-step>
      <v-stepper-content step="4">
        <Step4 />
      </v-stepper-content>

      <v-stepper-step editable step="5" color="success">Hilfe?</v-stepper-step>
      <v-stepper-content step="5">
        <Step5 />
      </v-stepper-content>
    </v-stepper>

    <!-- DESKTOP / TABLET -->
    <v-stepper v-model="currentStep" v-else class="fill-height-vh pt-12">
      <v-stepper-header>
         <v-stepper-step editable step="1" color="success"><span class="text-center">Angaben zum Umzug</span></v-stepper-step>
         <v-divider/>
         <v-stepper-step editable step="2" color="success"><span class="text-center">Wer zieht um?</span></v-stepper-step>
         <v-divider/>
         <v-stepper-step editable step="3" color="success"><span class="text-center">Wer soll informiert werden?</span></v-stepper-step>
         <v-divider/>
         <v-stepper-step editable step="4" color="success"><span class="text-center">Alles unter Kontrolle</span></v-stepper-step>
         <v-divider/>
         <v-stepper-step editable step="5" color="success"><span class="text-center">Hilfe?</span></v-stepper-step>
      </v-stepper-header>
      <v-stepper-items>
        <v-stepper-content step="1">
