<script>
import Toolbar from '@/components/Toolbar'

import Step1 from '@/components/move/step1/Step1'
import Step2 from '@/components/move/step2/Step2'
import Step3 from '@/components/move/step3/Step3'
import Step4 from '@/components/move/step4/Step4'
import Step5 from '@/components/move/step5/Step5'

export default {
  name: 'move-steps',
  components: {
    Step1,
    Step2,
    Step3,
    Step4,
    Step5,
    Toolbar
  },
  computed: {
    currentStep: {
      get() {
        return this.$store.getters.currentStep
      },
      set(value) {
        this.$store.commit('updateProperty', { path: `currentStep`, value })
      }
    }
  }
}
</script>

<style scoped>
.fill-height-vh {
  height: 100vh;
