<template>
  <div class="d-flex flex-row">
    <v-btn v-if="showPrev" class="mt-8" color="success" icon @click="prevStep"><v-icon>mdi-arrow-left</v-icon></v-btn>
    <div class="ml-auto" />
    <v-btn v-if="showNext" class="mt-8" color="success" icon @click="nextStep"><v-icon>mdi-arrow-right</v-icon></v-btn>
  </div>
</template>

<script>
export default {
  name: 'move-next-prev-bar',
  props: {
    'showNext': Boolean,
    'showPrev': Boolean
  },
  methods: {
    prevStep() {
      this.$store.commit('prevStep')
    },
    nextStep() {
      this.$store.commit('nextStep')
    }
  }
}
</script>

