<template>
  <div class="wrapped">
    <span>Bitte erfassen Sie Ihre Personalien sowie allenfalls die Angaben zu weitere Personen, welche mit Ihnen umziehen.</span>

    <div :class="{'d-flex': true, 'flex-row': !$vuetify.breakpoint.xsOnly, 'flex-column': $vuetify.breakpoint.xsOnly, 'full-width': true}">
      <div class="d-flex flex-column full-width mt-3">
        <span class="title">Meine Angaben</span>
        <Person class="ma-1" path="persons.creator" />
      </div>
      <div :class="{ 'ml-10': !$vuetify.breakpoint.xsOnly, 'cards-width': true, 'mt-10': !$vuetify.breakpoint.xsOnly && $store.getters.persons.further.length < 1, 'mt-3': !(!$vuetify.breakpoint.xsOnly && $store.getters.persons.further.length < 1) }">
        <div v-for="(_, idx) in $store.getters.persons.further" :key="idx" class="mt-4">
          <div class="d-flex flex-row">
            <span class="title">{{ idx + 1 }}. weitere Person</span>
            <div class="ml-auto" />
            <v-btn icon color="error" class="mr-3" @click="removeFromPersons(idx)"><v-icon>mdi-account-minus-outline</v-icon></v-btn>
          </div>
          <Person class="ma-1" :path="`persons.further.${idx}`" />
        </div>
        
        <v-btn class="ma-2 card-sized" tile outlined color="success" @click="createFurtherPerson">
          <v-icon left>mdi-account-plus-outline</v-icon> Weitere Person anfgen
        </v-btn>
      </div>
    </div>

    <NextPrevBar :show-next="true" :show-prev="true" />
  </div>
</template>

<script>
import Person from '@/components/move/step2/Person'

import NextPrevBar from '@/components/move/NextPrevBar'

export default {
  name: 'move-step2',
  components: {
    Person,
    NextPrevBar
  },
  methods: {
    createFurtherPerson() {
      this.$store.commit('createPerson')
    },
    removeFromPersons(idx) {
      this.$store.commit('removePerson', idx)
    }
  }
}
</script>

<style scoped>
.card-sized {
  width: calc(95% - 8px);
  max-width: calc(350px - 8px);
}

.cards-width {
  width: 100%;
  max-width: 350px;
}
</style>